<template>
  <div>
    <van-checkbox-group
      class="card-goods"
      v-model="checkedGoods"
      ref="checkboxGroup"
    >
      <li v-for="item in goods" :key="item.prodId">
        <van-checkbox :name="item.prodId"></van-checkbox>
        <van-card
          :title="item.prodName"
          :price="$util.priceAndPointsShow(item.unitPrice,item.unitPoints)"
          :thumb="item.img||'http://d.umq.cn/weixin/images/nopic.png'"
          :centered="true"
        >
          <div slot="num">
            <van-stepper
              class="goods-buy-num"
              v-model="item.num"
              integer
              min="1"
              max="9"
            />
          </div>
        </van-card>
      </li>
    </van-checkbox-group>
    <van-submit-bar
      :disabled="!checkedGoods.length"
      :button-text="submitBarText"
      @submit="onSubmit"
    >
    <van-checkbox v-model="checkAll" @click="onClickCheckAll"
        >全选</van-checkbox>
    <div class="sel-pp">{{totalPrice}}</div>
    </van-submit-bar>
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup, Card, SubmitBar, Stepper } from "vant";
import { cart } from "@/mix/cart";
export default {
  name: "cart",
  mixins: [cart],
  components: {
    [Card.name]: Card,
    [Checkbox.name]: Checkbox,
    [SubmitBar.name]: SubmitBar,
    [CheckboxGroup.name]: CheckboxGroup,
    [Stepper.name]: Stepper
  },

  data() {
    return {
      checkedGoods: [],
      checkAll: false,
      goods: [],
    };
  },

  computed: {
    submitBarText() {
      const count = this.checkedGoods.length;
      return "结算" + (count ? `(${count})` : "");
    },
    totalPrice() {
      let totalPoints = 0
      let totalPrices = 0 
      for(let i=0;i<this.goods.length;i++){
          let item = this.goods[i]
          if(this.checkedGoods.indexOf(item.prodId) !== -1){
            totalPoints += parseInt(item.unitPoints)*item.num
            totalPrices += parseInt(item.unitPrice)*item.num
          }
      }
      return this.$util.priceAndPointsShow(totalPrices,totalPoints)
    }
  },

  mounted() {
    this.goods = this.readCartData();
  },

  watch: {
    //深度 watcher
    checkedGoods: {
      handler: function(val) {
        if (val.length === this.goods.length && val.length > 0) {
          this.checkAll = true;
        } else {
          this.checkAll = false;
        }
      },
      // deep: true
    }
  },

  methods: {
    onSubmit() {
      let selGoods = [];
      let payPoints = 0
      let payAmount = 0
      for(let i=0;i<this.goods.length;i++){
          let item = this.goods[i]
          if(this.checkedGoods.indexOf(item.prodId) !== -1){
            selGoods.push({
              "unitPrice":item.unitPrice,
              "unitPoints":item.unitPoints,
              "num":item.num,
              "prodId":item.prodId,
              "prodName":item.prodName
            })
            payPoints += parseInt(item.unitPoints)*item.num
            payAmount += parseInt(item.unitPrice)*item.num
          }
      }
      if(this.judgeSignIn()){
        this.createOrder(selGoods,payPoints,payAmount);
      }
    },

    onClickCheckAll() {
      let that = this;
      if (that.checkAll) {
         //实现反选
         this.$refs.checkboxGroup.toggleAll();
      } else {

        //实现全选
        this.$refs.checkboxGroup.toggleAll(true);
      }
    }
  }
};
</script>

<style lang="less" scope="true">
.card-goods {
  padding: 10px 10px;
  background-color: #fff;
  .van-card {
    width: 90%;
  }
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fafafa;
    margin-bottom: 10px;
  }
  .van-card__price {
    color: #f44;
  }
}
.van-checkbox {
  margin-left: 3%;
}
.sel-pp{
  width: 45%;
  margin-left: 3%;
  text-align: center;
  color: #f44;
}
</style>
